<script>
export default {
  name: "detailbaseinfo",
  data() {
    return {
     
    };
  },
  methods: {
    ff(index) { //注意：返回值可直接在标签中显示！！！！
      let ss = this.shop.score[index - 1].score;
      if (ss >= 5) {
        return "高";
      } else {
        return "低";
      }
    },
  },
  props: {
    shop: {
      type: Object,
      default: {},
    },
