<template>

<div class="goodsitemless"  @click="btn">
   
    <img class="tupianan" v-lazy="showimage" @load="lmageload" ><!--图片加载完在执行的事件-->
    
    <p>{{goodsitem.title}}</p>
 
     <span class="price">{{goodsitem.price}}</span>
     <span class="asr"><img src="../../../assets/img/shouye/collect1.svg" alt="" ></span>
    <span class="collect">{{goodsitem.cfav}}</span>
   </div>

</template>
<script> 
export default {
    name:"Gooditemless",
    props: {
        goodsitem:{
            type:Object
        }
    },
    computed:{
showimage(){
    return this.goodsitem.image||this.goodsitem.show.img
}
    },
  methods:{
      btn(){

     this.goodsitem.iid&&this.$router.push('/detail/'+this.goodsitem.iid)
        },
      
        lmageload(){
//事件总线：不是用于管理状态的，适用于管理事件的，发射事件：this.$bus.$emit('aaa'),监听事件：this.$bus.$on('aaa)，适用于不存在父子关系的组件
//且要在main.js中挂载上，Vue.prototype.$bus=new Vue()   
  
this.$emit('aaa')

         //this.$emit('aaa')
        }
  }
}
</script>
<style>
.tupianan{
    width: 100%;
    border-radius: 6px;
}
.goodsitemless{
    display:inline-block;
    width: 48%;
   margin-left:5px;
   text-align: center;
 position: relative;
    }
   
  
    p{/*文字省略三段式 */
     overflow: hidden;/*溢出隐藏*/
text-overflow:ellipsis;/*溢出部分显示为省略号*/
white-space: nowrap;/*不换行*/
    }
    .price{
        color:red;
       
    }
    .asr img{
        position: absolute;
        width: 20px;
        height: 20px;
       bottom:2px;
    }
    .collect{
        margin-left:21px;
    }
   
</style>