<template> 
  <div class="detail-zhu animate__animated animate__fadeIn">
    <detailnaver @detailnaver="detailnavers" ref="detailnavers"></detailnaver>
    <scroll class="content" ref="scroll" @sscroll="huadong" >
      <detaillunbo ref="rshangping" :tupian="tipian"></detaillunbo>
      <detailinfo :good="goods"></detailinfo>
      <detailbaseinfo :shop="shops"></detailbaseinfo>
      <detaildianpu :dianpuxing="dianpu" @detailload="ser"></detaildianpu>
      <detailcanshu ref="rcanshu" :canshus="canshu"></detailcanshu>
      <detailpinglun
        ref="rpinglun"
        :detailpingluns="commentinfo"
      ></detailpinglun>
      <gooditem ref="rtuijian" :goods="recommend" @sde="ser"></gooditem>
    </scroll>
    <detailbottom @gouwu="jiashang"></detailbottom>
    <topwid @click.native="gotop" v-show="isshow"></topwid>
  </div>
</template>
<script>
import detailnaver from "./detailzi/detailnaver";
import { getdetail, Goods, Shop, getrecommend } from "../../network/detail";
import detailinfo from "./detailzi/detailinfo.vue";
import detailbaseinfo from "./detailzi/detailbaseinfo.vue";
import detaillunbo from "./detailzi/detaillunbo.vue";
import detaildianpu from "./detailzi/detaildianpu.vue";
import Scroll from "../../components/content/Scroll.vue";
import detailcanshu from "./detailzi/detailcanshu.vue";
import detailpinglun from "./detailzi/detailpinglun.vue";
import Gooditem from "../../components/content/goodss/Gooditem.vue";
import detailbottom from "./detailzi/detailbottom.vue"
import Topwid from "../../components/content/Topwid.vue"
import {debounce} from "../../components/common/utils.js"
export default {
  name: "Detail",
  components: {
    detailnaver,
    detaillunbo,
    detailinfo,
    Scroll,
    detaildianpu,
    detailbaseinfo,
    detailcanshu,
    detailpinglun,
    Gooditem,
    detailbottom,
    Topwid
  },

  data() {
    return {
      id: null,
      tipian: [],
      goods: {},
      shops: {},
      dianpu: {},
      canshu: {},
      commentinfo: {},
      recommend: [],
      rnaver: [],
      isshow:false,
      timer:null
    }
  },
  created() {
    this.id= this.$route.params.id;
    getdetail(this.id).then((res) => {
      //定义公共部分
      const data = res.data.result;
      //console.log(data)
      //轮播图数据处理
      this.tipian = data.itemInfo.topImages;
      //价格模块数据处理
      this.goods = new Goods(
        data.itemInfo,
        data.columns,
        data.shopInfo.services
      );
      //base模块数据处理
      this.shops = new Shop(data.shopInfo);
      //店铺数据处理
      this.dianpu = data.detailInfo.detailImage[0];
      //商品参数信息处理
      this.canshu = data.itemParams;
      //评论信息处理
      if (data.rate.cRate !== 0) {
        this.commentinfo = data.rate.list[0];
      }
      //
      
    });
      //推荐信息处理
    getrecommend(this.id).then((res) => {
      this.recommend = res.data.data.list;
    });
  },
  mounted(){
    this.timer=debounce(this.aase,500)
  },
  methods: {
    //
     jiashang(){
const a={};
a.image=this.tipian[0];
a.title=this.goods.title;
a.desc=this.goods.desc;
a.price=this.goods.realPrice;
a.id=this.id;
a.isshow=false;
this.$store.commit('arrjia',a)
    },
        //计算商品，评论，价格，推荐的offsetTop
        aase(){
      this.$refs.scroll.refresh();
      this.rnaver = [];
      this.rnaver.push(this.$refs.rshangping.$el.offsetTop);
      this.rnaver.push(this.$refs.rcanshu.$el.offsetTop);
      this.rnaver.push(this.$refs.rpinglun.$el.offsetTop);
      this.rnaver.push(this.$refs.rtuijian.$el.offsetTop);
     
        },
    //图片加载完计算高度
    ser() {
      this.timer();
    },
    //滑动事件
    huadong(position) {
      //滑动naver对应
      for (let i = 0; i < this.rnaver.length; i++) {
        if (position.y <= -this.rnaver[3]) {
          this.$refs.detailnavers.curr = 3;
        } else if (
          position.y <= -this.rnaver[i] &&
          position.y >= -this.rnaver[i + 1]
        ) {
          this.$refs.detailnavers.curr = i;
        }
      }
      //topwid隐藏
      if(position.y<=-800){
        this.isshow=true
      }else{
        this.isshow=false
      }
    },
    //naver点击事件
    detailnavers(index) {
      let naverheight = -this.rnaver[index];
      this.$refs.scroll.scroll.scrollTo(0, naverheight, 100);
    },
    gotop(){
      this.$refs.scroll.scroll.scrollTo(0,0,300)
    }
  },
};
</script>
<style scoped>
.detail-zhu {
  height: 100vh;
  position: relative;
  z-index: 99;
  background-color: white;
}
.content {
  height: calc(100% - 94px);
  position: fixed;
  top: 44px;
  width: 100%;
}
</style>